import { body } from '../../config/common'

export default (props) => {
    const test = () => {
        if (document) {
            document.body.style = body;
        } else {
            setTimeout(() => {
                test()
            }, 500);
        }
    }

    test()
    // document.body.style = body;
    return <section>
        <style jsx>{`
            *:before, *:after {
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
            }
            html {
                -webkit-text-size-adjust: 100%; /* 2 */
            }
            body {
                margin: 0;
                font-size: 16px;
                font-family: sans-serif;
                font-weight: 300;
                color: #666;
                letter-spacing: 0.03rem;
                background: #f4f7f8;
            }
        `}</style>
        { props.children }
    </section>
}